<template>
    <div class="mt-5">
        <h1>Formularios</h1>
        <form>
            <b-row>
                <b-col md="3">
                    <b-form-input type="text" v-model="text" :state="comprobar"></b-form-input>
                    <small v-if="!comprobar">Mínimo 3 carácteres</small>
                </b-col>
                <b-col md="3">
                    <b-form-select v-model="select" :options="animales"></b-form-select>
                    <p>Selección: {{ select }} </p>
                </b-col>
                <b-col md="3">
                    <b-form-radio-group v-model="radio" :options="animalesRadio"></b-form-radio-group>
                    <p> {{ radio }} </p>
                </b-col>
                <b-col md="3">
                    <b-form-checkbox-group v-model="checkbox" :options="animalesRadio"></b-form-checkbox-group>
                    <p>Multiple: {{checkbox }} </p>
                </b-col>
            </b-row>
        </form>
        <br>
        <p>Texto: {{ text }} </p>
    </div>
</template>

<script>
export default {
    name:'Form',
    data(){
        return {
            text:'',
            checkbox:[],
            radio:null,
            select: null,
            animales:[
                {value: null, text:'Seleccione un animal'},
                {value:'perro', text:'Guau guau' },
                {value:'gato', text:'Miau' },
                {value:'pato', text:'Cuack' }
            ],
             animalesRadio:[
                {value:'perro', text:'Guau guau' },
                {value:'gato', text:'Miau' },
                {value:'pato', text:'Cuack' },
                {value:'gallo', text:'Kikiriki'}
            ]
        }
    },

    computed: {
        comprobar(){
            return this.text.length > 2 ? true : false
        }
    }
}
</script>